@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

*{outline:0}body{font-family:"Nunito", sans-serif;background-color:#e9e2d0;display:flex;flex-direction:column;align-items:center;justify-content:center}body header{position:relative}body header h1{color:#000;text-align:center}body header img{position:absolute;height:50px;width:50px}.metronome{text-align:center;max-width:400px;margin:0 auto;padding:10px}.bpm-slider{display:flex;font-size:2rem}.bpm-slider input{width:100%;margin:8px 20px;height:3rem;-ms-grid-row-align:center;align-self:center}.systemspecs button,.metronome button{background-color:transparent;border:none;cursor:pointer;font-size:3rem;height:100%}.metronome button.startStop img{width:70px;margin-bottom:10px}.systemspecs button,.bpm-measure button{background:#4e4e4e;padding:10px;margin:5px;border:1px solid #424242;border-radius:2px;width:100px;color:#000;font-size:20px}.systemspecs button:hover,.metronome button.active{background-color:#8d8d8d;border-color:#777777}.systemspecs{border-radius:5px;border:solid 2px #6e5773;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.systemspecs ul{margin:25px;align-self:flex-start;list-style:square}.systemspecs button{margin-bottom:25px}.navbar{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-direction:row;margin:50px}.navbar .container-link{border-radius:5px;border:solid 3px #6e5773;margin:10px}.navbar .nav-link{display:flex;flex-direction:column;align-items:center;justify-content:center;text-decoration:none;color:#000;padding:5px;text-align:center}

